div.bar {
    transition: all 1s ease-out;
}
div.bar:hover,div.bar.hover {
    transform: scale(1.2);
}
svg .arc {
    transition: all 1s ease-out;
}
svg .arc text {
    opacity: 0;
    pointer-events: none;
}
svg .arc.hover {
    transform: scale(1.2);
}
svg .arc.hover text, svg .arc:hover text{
    opacity: 1;
}